import { generateUUID, getFormattedDateTime } from "../../utils/Common";

export type Term = {
  termNumber: number;
  name: string;
  yearFrom: number;
  yearTo: number;
  createdAt?: string;
  createdBy?: string;
  updatedAt?: string;
  updatedBy?: string;
};

export const getTerms = async (): Promise<Term[]> => {
  await new Promise((res) => {
    setTimeout(res, 100);
  });

  const storeRaw: string = sessionStorage.getItem(storeName) || "{}";
  const store: Store = JSON.parse(storeRaw);

  if (storeRaw === "{}") {
    store["21"] = { termNumber: 21, name: "21期", yearFrom: 2021, yearTo: 2022 };
    store["22"] = { termNumber: 22, name: "22期", yearFrom: 2022, yearTo: 2023 };
    store["23"] = { termNumber: 23, name: "23期", yearFrom: 2023, yearTo: 2024 };
    store["24"] = { termNumber: 24, name: "24期", yearFrom: 2024, yearTo: 2025 };
    store["25"] = { termNumber: 25, name: "25期", yearFrom: 2025, yearTo: 2026 };
    sessionStorage.setItem(storeName, JSON.stringify(store));
  }

  const pairs: [string, Term][] = Object.entries(store);
  const terms: Term[] = pairs.map((e: [string, Term]) => e[1]);

  return terms;
};
// backendが構築できるまでの代わり
export const storeName = "terms";
export type Store = { [key: string]: Term };
