import React, { useEffect, useState } from "react";

import { useLocation } from "react-router-dom";

import { Stack, Grid, IconButton, Divider } from "@mui/material";
import AddCircleOutlineIcon from "@mui/icons-material/AddCircleOutline";

import MemberEdit from "./components/MemberEdit";

import Circular from "../../components/CustomCircular";
import SubWindow from "../../components/SubWindow";
import StickyHeaderTable, { Column } from "../../components/table/StickyHeaderTable";

import { errorDefault } from "../../utils/Toastify";

import {
  // api
  getMembers,
  // model
  Member,
  SourcingType,
  ContractType,
  Gender,
} from "../../stores/cost/MembersStoreAdapter";

const MemberPage: React.FC = () => {
  const location = useLocation();

  const [isProgress, setProgress] = useState(true);

  const [isCreateOpen, setCreateOpen] = useState(false);
  const [isEditOpen, setEditOpen] = useState(false);

  const [members, setMembers] = useState<Member[]>([]);
  const [editMember, setEditMember] = useState<Member>();

  useEffect(() => {
    getMembers()
      .then((result: Member[]) => {
        setMembers(result);
        setProgress(false);
      })
      .catch((e: Error) => {
        setProgress(false);
        errorDefault("想定外エラー!!");
      });
  }, []);

  const handleUpdate = () => {
    getMembers()
      .then((result: Member[]) => {
        setMembers(result);
        setProgress(false);
      })
      .catch((e: Error) => {
        setProgress(false);
        errorDefault("想定外エラー!!");
      });
  };

  const handleCreateOpen = () => {
    setCreateOpen(true);
  };

  const handleCreateClose = () => {
    setCreateOpen(false);
  };

  const handleEditOpen = (member: Member) => {
    setEditMember(member);
    setEditOpen(true);
  };

  const handleEditClose = () => {
    setEditOpen(false);
  };

  return (
    <>
      <Stack spacing={5} divider={<Divider flexItem />}>
        <Grid container spacing={1}>
          <Grid size={0.5}></Grid>
          <Grid size={2}>
            <IconButton onClick={handleCreateOpen}>
              <AddCircleOutlineIcon fontSize="large" />
            </IconButton>
          </Grid>
          <Grid size={9.5}></Grid>
        </Grid>
        <StickyHeaderTable headers={headers} records={members} clickAction={handleEditOpen} />
      </Stack>
      <SubWindow title="要員の登録" open={isCreateOpen} handleClose={handleCreateClose}>
        <MemberEdit
          mode="c"
          handleUpdate={handleUpdate}
          handleClose={handleCreateClose}
          setProgress={setProgress}
        />
      </SubWindow>
      <SubWindow title="要員の編集" open={isEditOpen} handleClose={handleEditClose}>
        <MemberEdit
          member={editMember}
          mode="u"
          handleUpdate={handleUpdate}
          handleClose={handleEditClose}
          setProgress={setProgress}
        />
      </SubWindow>
      <Circular open={isProgress} />
    </>
  );
};

const headers: readonly Column[] = [
  { id: "name", label: "氏名", minWidth: 200, align: "center" },
  { id: "kana", label: "氏名(カナ)", minWidth: 200, align: "center" },
  {
    id: "sourcingType",
    label: "雇用形態",
    minWidth: 100,
    align: "center",
    format: (v: Member) => SourcingType[v.sourcingType],
  },
  {
    id: "affiliation",
    label: "所属",
    minWidth: 150,
    align: "center",
    format: (v: Member) => {
      if ("1" === v.sourcingType) {
        return v.affiliation;
      }
      if ("2" === v.sourcingType) {
        return v?.partner?.name;
      }

      return "-";
    },
  },
  {
    id: "contractType",
    label: "契約形態",
    minWidth: 150,
    align: "center",
    format: (v: Member) => ContractType[v.contractType],
  },
  {
    id: "gender",
    label: "性別",
    minWidth: 100,
    align: "center",
    format: (v: Member) => Gender[v.gender],
  },
  { id: "createdAt", label: "登録日", minWidth: 150, align: "center" },
  { id: "createdBy", label: "登録者", minWidth: 150, align: "center" },
  { id: "updatedAt", label: "最終更新日", minWidth: 150, align: "center" },
  { id: "updatedBy", label: "最終更新者", minWidth: 150, align: "center" },
];

export default MemberPage;
