import React from "react";

import { useLocation } from "react-router-dom";

import { useForm, SubmitHandler, Controller } from "react-hook-form";

import { successDefault, errorDefault } from "../../../utils/Toastify";
import {
  TextField,
  Stack,
  Button,
  Container,
  Radio,
  RadioGroup,
  FormControlLabel,
  FormControl,
  FormLabel,
} from "@mui/material";

import {
  // api
  createPartner,
  updatePartner,
  // model
  Partner,
  PartnerType,
} from "../../../stores/cost/PartnersStoreAdapter";

import { nameRule, kanaRule, noteRune } from "../../../rule/Validation";

export interface PartnerEditProps {
  mode: "c" | "r" | "u";
  partner?: Partner;
  handleUpdate: any;
  handleClose: any;
  setProgress: any;
}

const PartnerEdit: React.FC<PartnerEditProps> = ({
  mode,
  partner,
  handleUpdate,
  handleClose,
  setProgress,
}) => {
  const location = useLocation();

  const {
    control,
    handleSubmit,
    reset,
    formState: { errors },
  } = useForm<Partner>({
    defaultValues: {
      name: partner?.name,
      kana: partner?.kana,
      partnerType: partner ? partner.partnerType : "1",
      corporateNumber: partner?.corporateNumber,
      invoiceRegistrationNumber: partner?.invoiceRegistrationNumber,
      url: partner?.url,
      note: partner?.note,
    },
  });

  const validationRules = {
    name: nameRule,
    kana: kanaRule,
    corporateNumber: {
      maxLength: { value: 20, message: "20文字以内で入力してください。" },
    },
    invoiceRegistrationNumber: {
      maxLength: { value: 20, message: "20文字以内で入力してください。" },
    },
    url: {
      maxLength: { value: 1024, message: "桁数が多すぎます" },
    },
    note: noteRune,
  };

  const onSubmit: SubmitHandler<Partner> = (input: Partner) => {
    setProgress(true);

    if ("c" === mode) {
      createPartner([input])
        .then((result: void) => {
          setProgress(false);
          successDefault("登録成功!!");
          handleClose();
          handleUpdate();
        })
        .catch((e: Error) => {
          setProgress(false);
          errorDefault("想定外エラー!!");
          handleClose();
        });
    }

    if ("u" === mode) {
      input.partnerId = partner!.partnerId!;

      updatePartner(input)
        .then((result: void) => {
          setProgress(false);
          successDefault("更新成功!!");
          handleClose();
          handleUpdate();
        })
        .catch((e: Error) => {
          setProgress(false);
          errorDefault("想定外エラー!!");
          handleClose();
        });
    }
  };

  return (
    <>
      <Container>
        <form onSubmit={handleSubmit((d) => onSubmit(d))} noValidate>
          <Stack direction="column" width="500px" gap={2}>
            <Stack direction="column" gap={1}>
              <FormLabel id="name-group-label">氏名</FormLabel>
              <Stack direction="row" gap={2}>
                <Controller
                  name="name"
                  control={control}
                  rules={validationRules.name}
                  render={({ field, formState: { errors } }) => (
                    <TextField
                      variant="outlined"
                      disabled={"r" === mode}
                      {...field}
                      label="氏名"
                      error={!!errors.name}
                      helperText={errors.name?.message}
                    />
                  )}
                />
                <Controller
                  name="kana"
                  control={control}
                  rules={validationRules.kana}
                  render={({ field, formState: { errors } }) => (
                    <TextField
                      variant="outlined"
                      disabled={"r" === mode}
                      {...field}
                      label="氏名(カナ)"
                      error={!!errors.kana}
                      helperText={errors.kana?.message}
                    />
                  )}
                />
              </Stack>
            </Stack>
            <Controller
              name="partnerType"
              control={control}
              render={({ field, formState: { errors } }) => (
                <FormControl>
                  <FormLabel id="partner-type-group-label">法人/個人事業主</FormLabel>
                  <RadioGroup disabled={"r" === mode} {...field} row name="row-radio-buttons-group">
                    <FormControlLabel value="1" control={<Radio />} label={PartnerType["1"]} />
                    <FormControlLabel value="2" control={<Radio />} label={PartnerType["2"]} />
                  </RadioGroup>
                </FormControl>
              )}
            />
            <Controller
              name="corporateNumber"
              control={control}
              rules={validationRules.corporateNumber}
              render={({ field, formState: { errors } }) => (
                <TextField
                  variant="outlined"
                  disabled={"r" === mode}
                  {...field}
                  label="法人番号"
                  error={!!errors.corporateNumber}
                  helperText={errors.corporateNumber?.message}
                />
              )}
            />
            <Controller
              name="invoiceRegistrationNumber"
              control={control}
              rules={validationRules.invoiceRegistrationNumber}
              render={({ field, formState: { errors } }) => (
                <TextField
                  variant="outlined"
                  disabled={"r" === mode}
                  {...field}
                  label="登録番号"
                  error={!!errors.invoiceRegistrationNumber}
                  helperText={errors.invoiceRegistrationNumber?.message}
                />
              )}
            />
            <Controller
              name="url"
              control={control}
              rules={validationRules.url}
              render={({ field, formState: { errors } }) => (
                <TextField
                  variant="outlined"
                  disabled={"r" === mode}
                  {...field}
                  label="企業ホームページ"
                  error={!!errors.url}
                  helperText={errors.url?.message}
                />
              )}
            />
            <Controller
              name="note"
              control={control}
              rules={validationRules.note}
              render={({ field, formState: { errors } }) => (
                <TextField
                  variant="outlined"
                  disabled={"r" === mode}
                  multiline
                  rows={7}
                  {...field}
                  label="備考"
                  error={!!errors.note}
                  helperText={errors.note?.message}
                />
              )}
            />
            <Button type="submit" variant="contained" color="primary">
              登録
            </Button>
          </Stack>
        </form>
      </Container>
    </>
  );
};

export default PartnerEdit;
