import React from "react";

import { useNavigate } from "react-router-dom";

import { useForm, SubmitHandler, Controller } from "react-hook-form";

import { Button, TextField, FormControl, Stack } from "@mui/material";
import { styled } from "@mui/system";

import Circular from "../../components/CustomCircular";

import { errorDefault } from "../../utils/Toastify";

import {
  // api
  getSessionId,
} from "../../stores/common/AuthenticationStoreAdapter";

import { APP_NAME } from "../../layouts/router/common";

type Inputs = {
  userId: string;
  password: string;
};

const Login: React.FC = () => {
  const [isProgress, setProgress] = React.useState(false);

  const {
    control,
    handleSubmit,
    formState: { errors },
  } = useForm<Inputs>({
    defaultValues: { userId: "", password: "" },
  });

  const validationRules = {
    userId: {
      required: "userIdを入力してください。",
      maxLength: { value: 20, message: "20文字以内で入力してください。" },
      pattern: {
        value: /^[a-zA-Z0-9]+$/,
        message: "半角英数字で入力してください。",
      },
    },
    password: {
      required: "passwordを入力してください。",
      maxLength: { value: 20, message: "20文字以内で入力してください。" },
      pattern: {
        value: /^[a-zA-Z0-9]+$/,
        message: "半角英数字で入力してください。",
      },
    },
  };

  const handleLogin: SubmitHandler<Inputs> = (inputs: Inputs) => {
    setProgress(true);
    getSessionId(inputs.userId, inputs.password)
      .then((result: string | null) => {
        if (!result) {
          setProgress(false);
          errorDefault("ログイン失敗");
          return;
        }
        sessionStorage.setItem("session-user", result);
        setProgress(false);
        document.location = `/${APP_NAME}`;
      })

      .catch((e: Error) => {
        setProgress(false);
        errorDefault("想定外エラー!!");
        console.error(e);
      });
  };

  return (
    <>
      <Stack component="form" noValidate onSubmit={handleSubmit(handleLogin)}>
        <Container>
          ログイン可能ユーザ(パスワードは適当でOK) <br />
          ・user1(一般) <br />
          ・user2(課長1) <br />
          ・user3(課長2)
          <br />
          ・user4(部長)
          <br />
          <LoginCard>
            <Controller
              name="userId"
              control={control}
              rules={validationRules.userId}
              render={({ field }) => (
                <FormControl fullWidth>
                  <TextField
                    {...field}
                    type="text"
                    label="userId"
                    error={errors.userId !== undefined}
                    helperText={errors.userId?.message}
                    variant="standard"
                    margin="normal"
                    fullWidth
                  />
                </FormControl>
              )}
            />
            <Controller
              name="password"
              control={control}
              rules={validationRules.password}
              render={({ field }) => (
                <FormControl fullWidth>
                  <TextField
                    {...field}
                    type="password"
                    label="password"
                    error={errors.password !== undefined}
                    helperText={errors.password?.message}
                    variant="standard"
                    margin="normal"
                    fullWidth
                  />
                </FormControl>
              )}
            />
            <Button variant="contained" type="submit" color="primary" fullWidth>
              ログイン
            </Button>
          </LoginCard>
        </Container>
      </Stack>

      <Circular open={isProgress} />
    </>
  );
};

const Container = styled("div")({
  display: "flex",
  flexDirection: "column",
  alignItems: "center",
  justifyContent: "center",
  height: "100vh",
  backgroundColor: "#f0f0f0",
});

const LoginCard = styled("div")({
  backgroundColor: "#fff",
  padding: "20px",
  borderRadius: "10px",
  boxShadow: "0 0 10px rgba(0,0,0,0.1)",
});

export default Login;
