import { generateUUID, getFormattedDateTime } from "../../utils/Common";

export type Manager = {
  managerId: string;
  name: string;
  position: string;
  note?: string;
  createdAt?: string;
  createdBy?: string;
  updatedAt?: string;
  updatedBy?: string;
};

export const getManager = async (managerId: string): Promise<Manager> => {
  await new Promise((res) => {
    setTimeout(res, 100);
  });

  const storeRaw: string = sessionStorage.getItem(storeName) || "{}";
  const store: Store = JSON.parse(storeRaw);

  if ("{}" === storeRaw) {
    const userId = "system";
    const now = getFormattedDateTime();

    const managerA: Manager = { managerId: "user2", name: "課長一郎", position: "課長" };
    const managerB: Manager = { managerId: "user3", name: "課長二郎", position: "課長" };

    store[managerA.managerId] = managerA;
    store[managerB.managerId] = managerB;
  }

  return store[managerId];
};

export const getManagers = async (): Promise<Manager[]> => {
  await new Promise((res) => {
    setTimeout(res, 100);
  });

  const storeRaw: string = sessionStorage.getItem(storeName) || "{}";
  const store: Store = JSON.parse(storeRaw);

  const pairs: [string, Manager][] = Object.entries(store);
  const managers: Manager[] = pairs.map((e: [string, Manager]) => e[1]);

  return managers;
};

// backendが構築できるまでの代わり
export const storeName = "managers";
export type Store = { [key: string]: Manager };
