import { Satellite } from "@mui/icons-material";
import { generateUUID, getFormattedDateTime } from "../../utils/Common";

import { getTerms } from "./TermsStoreAdapter";

import { getMonths, Month } from "./MonthsStoreAdapter";
import { TeamMember } from "./TeamMembersStoreAdapter";

export type Result = {
  teamMemberId: string;
  termNumber: number;
  month: number;
  cost: number;
  sales: number;
  createdAt?: string;
  createdBy?: string;
  updatedAt?: string;
  updatedBy?: string;
};

export const createResults = async (teamMemberId: string): Promise<void> => {
  const storeRaw: string = sessionStorage.getItem(storeName) || "{}";
  const store: Store = JSON.parse(storeRaw);

  const montds = await getMonths();

  const userId = "tanaka";
  const now = getFormattedDateTime();

  const terms = await getTerms();
  const results: Result[] = [];

  for (const term of terms.sort((c1, c2) => c1.termNumber - c2.termNumber)) {
    for (const month of montds.sort((c1, c2) => c1.order - c2.order)) {
      results.push({
        teamMemberId: teamMemberId,
        termNumber: term.termNumber,
        month: month.month,
        cost: 0,
        sales: 0,
        createdAt: now,
        updatedAt: now,
        createdBy: userId,
        updatedBy: userId,
      });
    }
  }
  store[teamMemberId] = results;

  sessionStorage.setItem(storeName, JSON.stringify(store));
};

export const updateResults = async (
  teamMember: TeamMember,
  termNumber: number,
  results: Result[]
): Promise<void> => {
  await new Promise((res) => {
    setTimeout(res, 300);
  });

  const storeRaw: string = sessionStorage.getItem(storeName) || "{}";
  const store: Store = JSON.parse(storeRaw);

  const targets = store[teamMember.teamMemberId!] ?? [];

  const userId = "system";
  const now = getFormattedDateTime();

  const montds = await getMonths();
  const newResults: Result[] = [];
  for (const month of montds.sort((c1, c2) => c1.order - c2.order)) {
    const nr = results.find((e) => month.month === e.month);
    if (nr) {
      newResults.push(nr);
      continue;
    }

    const br = targets.find((e) => termNumber === e.termNumber && e.month === e.month);
    if (br) {
      newResults.push(br);
    }
  }

  store[teamMember.teamMemberId!] = newResults;

  sessionStorage.setItem(storeName, JSON.stringify(store));
};

export const getResults = async (teamMemberId: string): Promise<Result[]> => {
  const storeRaw: string = sessionStorage.getItem(storeName) || "{}";
  const store: Store = JSON.parse(storeRaw);

  return store[teamMemberId];
};

// backendが構築できるまでの代わり
export const storeName = "results";
export type Store = { [key: string]: Result[] };
