import { successDefault, errorDefault } from "../../../utils/Toastify";

import {
  // api
  getMembers,
  // model
  Member,
  SourcingType,
  ContractType,
  Gender,
} from "../../../stores/cost/MembersStoreAdapter";

import {
  // api
  createMember,
  // model
  TeamMember,
} from "../../../stores/cost/TeamMembersStoreAdapter";

export interface TeamMemberAddProps {
  mode: "c";
  teamId: string;
  handleUpdate: any;
  handleClose: any;
  setProgress: any;
}

const TeamMemberAdd: React.FC<TeamMemberAddProps> = ({
  mode,
  teamId,
  handleUpdate,
  handleClose,
  setProgress,
}) => {
  const location = useLocation();

  const [members, setMembers] = useState<Member[]>([]);

  const [member, setMember] = useState<Member | null>(null);

  useEffect(() => {
    getMembers()
      .then((result: Member[]) => {
        setMembers(
          result
            .map((e) => {
              const kanaFirst = e.kana[0].toUpperCase();
              e.kana = kanaFirst;
              return e;
            })
            .sort((a, b) => -b.kana.localeCompare(a.kana))
        );
      })
      .catch((e: Error) => {
        errorDefault("想定外エラー!!");
      });
  }, []);

  const onSubmit = () => {
    setProgress(true);

    const tm: TeamMember = {
      teamId: teamId,
      memberId: member!.memberId!,

      results: [],
    };
    createMember(tm)
      .then((result: void) => {
        setProgress(false);
        successDefault("登録成功!!");
        handleClose();
        handleUpdate();
      })
      .catch((e: Error) => {
        setProgress(false);
        errorDefault("想定外エラー!!");
        handleUpdate();
      });
  };

  return (
    <>
      <Container>
        <Stack direction="column" width="500px" gap={2}>
          <Grid container spacing={1}>
            <Grid size={9}>
              <Autocomplete
                value={member}
                fullWidth
                options={members}
                groupBy={(o) => o.kana}
                getOptionLabel={(o: Member) => o.name}
                onChange={(e, v) => {
                  setMember(v);
                }}
                renderInput={(params) => (
                  <TextField variant="outlined" {...params} label="追加メンバー" />
                )}
              />
            </Grid>
            <Grid size={1.5}></Grid>
            <Grid size={1.5}>
              <IconButton onClick={() => onSubmit()} size="large" disabled={!member}>
                <PersonAddIcon fontSize="large" />
              </IconButton>
            </Grid>
          </Grid>
          {member ? <ProfileCard member={member} /> : <></>}
          {/* <Stack direction="column" gap={1}>
            <FormLabel id="name-group-label">氏名</FormLabel>
            <Stack direction="row" gap={2}>
              <TextField
                variant="outlined"
                fullWidth
                disabled
                value={member ? member.name : ""}
                label="氏名"
              />
              <TextField
                variant="outlined"
                fullWidth
                disabled
                value={member ? member.kana : ""}
                label="氏名(カナ)"
              />
            </Stack>
          </Stack>
          <FormControl>
            <FormLabel id="sourcing-type-group-label">雇用形態</FormLabel>
            <RadioGroup value={member ? member.sourcingType : "1"} row name="sourcing-type-group">
              <FormControlLabel disabled value="1" control={<Radio />} label={SourcingType["1"]} />
              <FormControlLabel disabled value="2" control={<Radio />} label={SourcingType["2"]} />
            </RadioGroup>
          </FormControl>
          <div hidden={"2" === (member ? member.sourcingType : "1")}>
            <TextField
              fullWidth
              disabled
              value={member ? (member.affiliation ? member.affiliation : "") : ""}
              label="所属"
            />
          </div>
          <div hidden={"1" === (member ? member.sourcingType : "1")}>
            <TextField
              disabled
              fullWidth
              value={member ? (member.partner ? member.partner.name : "") : ""}
              label="企業名"
            />
          </div>
          <div hidden={"1" === (member ? member.sourcingType : "1")}>
            <FormControl>
              <FormLabel id="contract-type-group-label">契約形態</FormLabel>
              <RadioGroup value={member ? member.contractType : "1"} row name="contract-type-group">
                <FormControlLabel
                  disabled
                  value="1"
                  control={<Radio />}
                  label={ContractType["1"]}
                />
                <FormControlLabel
                  disabled
                  value="2"
                  control={<Radio />}
                  label={ContractType["2"]}
                />
                <FormControlLabel
                  disabled
                  value="3"
                  control={<Radio />}
                  label={ContractType["3"]}
                />
              </RadioGroup>
            </FormControl>
          </div>
          <FormControl>
            <FormLabel id="gender-group-label">性別</FormLabel>
            <RadioGroup value={member ? member.gender : "1"} row name="gender-group">
              <FormControlLabel disabled value="1" control={<Radio />} label={Gender["1"]} />
              <FormControlLabel disabled value="2" control={<Radio />} label={Gender["2"]} />
              <FormControlLabel disabled value="3" control={<Radio />} label={Gender["3"]} />
            </RadioGroup>
          </FormControl>
          <TextField
            variant="outlined"
            disabled
            fullWidth
            multiline
            rows={7}
            value={member ? (member.note ? member.note : "") : ""}
            label="備考"
          /> */}
        </Stack>
      </Container>
    </>
  );
};

export default TeamMemberAdd;
