import React, { useState, useEffect } from "react";
import { useNavigate, Outlet } from "react-router-dom";

import Stack from "@mui/material/Stack";
import Chip from "@mui/material/Chip";
import Typography from "@mui/material/Typography";
import WalletIcon from "@mui/icons-material/Wallet";

import { RouterTitle, RouterBreadcrumbs } from "./router/cost";
import { AppProvider, NavigationItem, Router } from "@toolpad/core/AppProvider";
import { PageContainer } from "@toolpad/core/PageContainer";
import { DashboardLayout } from "@toolpad/core/DashboardLayout";

import { dashboardTheme, session, signInOut, SidebarFooter } from "./router/common";
import { navigationItems } from "./navigation/cost";

import TeamEdit from "../pages/cost/components/TeamEdit";
import TeamMemberAdd from "../pages/cost/components/TeamMemberAdd";
import SubWindow from "../components/SubWindow";
import Circular from "../components/CustomCircular";

import {
  // model
  Team,
} from "../stores/cost/TeamsStoreAdapter";

import { APP_NAME } from "./router/common";

const CostLayout: React.FC = () => {
  const [navigation, setNavigation] = useState<NavigationItem[]>([]);

  const [update, setUpdate] = useState(true);
  const [isProgress, setProgress] = useState(false);

  const [teams, setTeams] = useState<{ [key: string]: Team }>({});

  const [editTeam, setEditTeam] = useState<Team | undefined>();
  const [teamCreateWindowOpen, setTeamCreateWindowOpen] = useState(false);
  const [memberAddWindowOpen, setMemberAddWindowOpen] = useState(false);

  const authentication = React.useMemo(() => signInOut, []);

  useEffect(() => {
    setUpdate(!update);
  }, []);

  useEffect(() => {
    navigationItems(
      setTeams,
      setNavigation,
      setEditTeam,
      handleTeamCreateWindowOpen,
      handleMemberAddWindowOpen
    );
  }, [update]);

  const router = useRouter(`/${APP_NAME}/cost`);

  const breadcrumbs = React.useMemo(() => {
    return RouterBreadcrumbs(router.pathname, teams);
  }, [router.pathname]);

  const title = React.useMemo(() => {
    return RouterTitle(router.pathname, teams);
  }, [router.pathname]);

  const handleTeamCreateWindowOpen = () => {
    setTeamCreateWindowOpen(true);
  };

  const handleTeamCreateWindowClose = () => {
    setTeamCreateWindowOpen(false);
  };

  const handleMemberAddWindowOpen = () => {
    setMemberAddWindowOpen(true);
  };

  const handleMemberAddWindowClose = () => {
    setMemberAddWindowOpen(false);
  };

  return (
    <>
      <AppProvider
        session={session()}
        authentication={authentication}
        navigation={navigation}
        router={router}
        theme={dashboardTheme}
      >
        <DashboardLayout
          defaultSidebarCollapsed
          sidebarExpandedWidth="50vh"
          slots={{
            appTitle: CustomAppTitle,
            sidebarFooter: SidebarFooter,
          }}
        >
          <PageContainer maxWidth="xl" title={title} breadcrumbs={breadcrumbs}>
            <Outlet />
          </PageContainer>
        </DashboardLayout>
        <SubWindow
          title="チームの作成"
          open={teamCreateWindowOpen}
          handleClose={handleTeamCreateWindowClose}
        >
          <TeamEdit
            mode="c"
            team={editTeam!}
            handleUpdate={() => setUpdate(!update)}
            handleClose={handleTeamCreateWindowClose}
            setProgress={setProgress}
          />
        </SubWindow>
        <SubWindow
          title="メンバーの追加"
          open={memberAddWindowOpen}
          handleClose={handleMemberAddWindowClose}
        >
          <TeamMemberAdd
            mode="c"
            teamId={editTeam?.teamId ?? ""}
            handleUpdate={() => setUpdate(!update)}
            handleClose={handleMemberAddWindowClose}
            setProgress={setProgress}
          />
        </SubWindow>
        <Circular open={isProgress} />
      </AppProvider>
    </>
  );
};

function useRouter(initialPath: string): Router {
  const navigate = useNavigate();
  const [pathname, setPathname] = React.useState(initialPath);

  const router = React.useMemo(() => {
    return {
      pathname,
      searchParams: new URLSearchParams(),
      navigate: (path: string | URL) => {
        navigate(String(path));
        setPathname(String(path));
      },
    };
  }, [pathname]);

  return router;
}

function CustomAppTitle() {
  return (
    <>
      <Stack direction="row" alignItems="center" spacing={2}>
        <WalletIcon fontSize="large" color="primary" />
        <Typography color="primary" variant="h6" sx={{ fontWeight: "bold", m: 1 }}>
          CostManagementSystem
        </Typography>
        <Chip size="small" label="BETA" color="warning" />
      </Stack>
    </>
  );
}

export default CostLayout;
