import * as React from "react";
import Paper from "@mui/material/Paper";
import Table from "@mui/material/Table";
import TableBody from "@mui/material/TableBody";
import TableCell from "@mui/material/TableCell";
import TableContainer from "@mui/material/TableContainer";
import TableHead from "@mui/material/TableHead";
import TablePagination from "@mui/material/TablePagination";
import TableRow from "@mui/material/TableRow";
import { format } from "path";

export interface Column {
  id: string;
  label: string;
  minWidth?: number;
  align: "right" | "center" | "left";
  format?: (value: any) => string | undefined;
}

export interface StickyHeaderTableProps {
  headers: readonly Column[];
  records: any[];
  clickAction?: any;
}

const StickyHeaderTable: React.FC<StickyHeaderTableProps> = ({ headers, records, clickAction }) => {
  const [page, setPage] = React.useState(0);
  const [rowsPerPage, setRowsPerPage] = React.useState(10);

  const handleChangePage = (event: unknown, newPage: number) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (event: React.ChangeEvent<HTMLInputElement>) => {
    setRowsPerPage(+event.target.value);
    setPage(0);
  };

  return (
    <Paper sx={{ width: "100%", overflow: "hidden" }}>
      <TableContainer sx={{ maxHeight: 440 }}>
        <Table stickyHeader aria-label="sticky table">
          <TableHead>
            <TableRow>
              {headers.map((c) => (
                <TableCell key={c.id} align={c.align} style={{ minWidth: c.minWidth }}>
                  {c.label}
                </TableCell>
              ))}
            </TableRow>
          </TableHead>
          <TableBody>
            {records.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage).map((r, index) => {
              return (
                <TableRow
                  hover={clickAction}
                  role="checkbox"
                  tabIndex={-1}
                  key={index}
                  onClick={() => {
                    if (clickAction) {
                      clickAction(r);
                    }
                  }}
                >
                  {headers.map((c) => {
                    const value = r[c.id];
                    return (
                      <TableCell key={c.id} align={c.align}>
                        {c.format ? (c.format(r) ? c.format(r) : "-") : value ? value : "-"}
                      </TableCell>
                    );
                  })}
                </TableRow>
              );
            })}
          </TableBody>
        </Table>
      </TableContainer>
      <TablePagination
        rowsPerPageOptions={[10, 25, 100]}
        component="div"
        count={records.length}
        rowsPerPage={rowsPerPage}
        page={page}
        onPageChange={handleChangePage}
        onRowsPerPageChange={handleChangeRowsPerPage}
      />
    </Paper>
  );
};

export default StickyHeaderTable;
