import { generateUUID, getFormattedDateTime } from "../../utils/Common";

import { getMember, Member } from "./MembersStoreAdapter";
import { createResults, updateResults, getResults, Result } from "./ResutsStoreAdapter";

export type TeamMember = {
  teamMemberId?: string;
  teamId: string;
  memberId: string;
  member?: Member;
  results: Result[];
  createdAt?: string;
  createdBy?: string;
  updatedAt?: string;
  updatedBy?: string;
};

export const createMember = async (member: TeamMember): Promise<void> => {
  await new Promise((res) => {
    setTimeout(res, 1000);
  });

  const storeRaw: string = sessionStorage.getItem(storeName) || "{}";
  const store: Store = JSON.parse(storeRaw);

  const teamMemberId = generateUUID();
  const userId = "tanaka";
  const now = getFormattedDateTime();
  member.teamMemberId = teamMemberId;

  const memberRaw = await getMember(member.memberId);
  member.member = memberRaw;
  member.createdBy = userId;
  member.updatedBy = userId;
  member.createdAt = now;
  member.updatedAt = now;
  store[teamMemberId] = member;

  // await createResults(teamMemberId);

  sessionStorage.setItem(storeName, JSON.stringify(store));
};

export const updateMember = async (member: TeamMember): Promise<void> => {
  await new Promise((res) => {
    setTimeout(res, 1000);
  });

  const storeRaw: string = sessionStorage.getItem(storeName) || "{}";
  const store: Store = JSON.parse(storeRaw);

  const targer = store[member.teamMemberId!];

  const userId = "yamada";
  const now = getFormattedDateTime();
  targer.updatedBy = userId;
  targer.updatedAt = now;
  store[member.teamMemberId!] = targer;

  sessionStorage.setItem(storeName, JSON.stringify(store));
};

export const getTeamMember = async (teamMemberId: string): Promise<TeamMember> => {
  await new Promise((res) => {
    setTimeout(res, 1000);
  });

  const storeRaw: string = sessionStorage.getItem(storeName) || "{}";
  const store: Store = JSON.parse(storeRaw);

  const member = store[teamMemberId];
  const results = (await getResults(teamMemberId)) ?? [];
  member.results = results;

  return member;
};

export const getTeamMembers = async (teamId: string): Promise<TeamMember[]> => {
  await new Promise((res) => {
    setTimeout(res, 1000);
  });

  const storeRaw: string = sessionStorage.getItem(storeName) || "{}";
  const store: Store = JSON.parse(storeRaw);

  const pairs: [string, TeamMember][] = Object.entries(store);
  const members: TeamMember[] = pairs
    .map((e: [string, TeamMember]) => e[1])
    .filter((e) => teamId === e.teamId);

  for (const member of members) {
    const results = (await getResults(member.teamMemberId!)) ?? [];
    member.results = results;
  }
  return members;
};

// backendが構築できるまでの代わり
export const storeName = "teamMembers";
export type Store = { [key: string]: TeamMember };
