import { createTheme } from "@mui/material/styles";

import Typography from "@mui/material/Typography";

import { SidebarFooterProps } from "@toolpad/core/DashboardLayout";

import { getSessionUser, clearSessionUser } from "../../stores/common/AuthenticationStoreAdapter";

export const APP_NAME = "ardito-inhouse-kanzhong-publish";

export const dashboardTheme = createTheme({
  colorSchemes: { light: true, dark: true },
  cssVariables: {
    colorSchemeSelector: "class",
  },
  breakpoints: {
    values: {
      xs: 0,
      sm: 600,
      md: 600,
      lg: 1200,
      xl: 1536,
    },
  },
});

export const session = () => {
  const sessionUser = getSessionUser();
  return {
    user: {
      name: sessionUser.name,
      email: sessionUser.userId,
      image: sessionUser.name[0],
    },
  };
};

export const signInOut = {
  signIn: () => {},
  signOut: () => {
    clearSessionUser();
    document.location = `/${APP_NAME}`;
  },
};

export function SidebarFooter({ mini }: SidebarFooterProps) {
  return (
    <Typography
      variant="caption"
      sx={{ m: 1, whiteSpace: "nowrap", overflow: "hidden", position: "absolute", bottom: 0 }}
    >
      {mini ? "© H.C" : `© ${new Date().getFullYear()} Made with love by Hiroki.Chigira`}
    </Typography>
  );
}
