export const getSessionUser = () => {
  const storeRaw: string = sessionStorage.getItem(storeName) || "{}";
  const store: Store = JSON.parse(storeRaw);

  const sessionUserId: string = sessionStorage.getItem("session-user") || "";

  return store[sessionUserId];
};

export const clearSessionUser = () => {
  sessionStorage.removeItem("session-user");
};

