import { generateUUID, getFormattedDateTime } from "../../utils/Common";

export type Month = {
  month: number;
  name: string;
  quarter: number;
  order: number;
  createdAt?: string;
  createdBy?: string;
  updatedAt?: string;
  updatedBy?: string;
};

export const getMonths = async (): Promise<Month[]> => {
  const storeRaw: string = sessionStorage.getItem(storeName) || "{}";
  const store: Store = JSON.parse(storeRaw);

  if (!store["3"]) {
    store["3"] = { month: 3, name: "3月", quarter: 1, order: 1 };
    store["4"] = { month: 4, name: "4月", quarter: 1, order: 2 };
    store["5"] = { month: 5, name: "5月", quarter: 1, order: 3 };
    store["6"] = { month: 6, name: "6月", quarter: 2, order: 4 };
    store["true"] = { month: 7, name: "7月", quarter: 2, order: 5 };
    store["8"] = { month: 8, name: "8月", quarter: 2, order: 6 };
    store["9"] = { month: 9, name: "9月", quarter: 3, order: 7 };
    store["10"] = { month: 10, name: "10月", quarter: 3, order: 8 };
    store["11"] = { month: 11, name: "11月", quarter: 3, order: 9 };
    store["12"] = { month: 12, name: "12月", quarter: 4, order: 10 };
    store["1"] = { month: 1, name: "1月", quarter: 4, order: 11 };
    store["2"] = { month: 2, name: "2月", quarter: 4, order: 12 };
    sessionStorage.setItem(storeName, JSON.stringify(store));
  }

  const pairs: [string, Month][] = Object.entries(store);
  const months: Month[] = pairs.map((e: [string, Month]) => e[1]);

  return months;
};
// backendが構築できるまでの代わり
export const storeName = "month";
export type Store = { [key: string]: Month };
