import React, { useState, useEffect } from "react";

import { useLocation } from "react-router-dom";

import { useForm, SubmitHandler, Controller } from "react-hook-form";

import {
  TextField,
  Stack,
  Button,
  Container,
  Radio,
  RadioGroup,
  FormControlLabel,
  FormControl,
  FormLabel,
  Autocomplete,
} from "@mui/material";

import { successDefault, errorDefault } from "../../../utils/Toastify";
import {
  // api
  createMembers,
  updateMember,
  // model
  Member,
  SourcingType,
  ContractType,
  Gender,
} from "../../../stores/cost/MembersStoreAdapter";

import {
  // api
  getPartners,
  // model
  Partner,
} from "../../../stores/cost/PartnersStoreAdapter";

import { nameRule, kanaRule, noteRune } from "../../../rule/Validation";

export interface MemberEditProps {
  mode: "c" | "r" | "u";
  member?: Member;
  handleUpdate: any;
  handleClose: any;
  setProgress: any;
}

const MemberEdit: React.FC<MemberEditProps> = ({
  mode,
  member,
  handleUpdate,
  handleClose,
  setProgress,
}) => {
  const location = useLocation();

  const [partners, setPartners] = useState<Partner[]>([]);

  useEffect(() => {
    getPartners()
      .then((result: Partner[]) => {
        setPartners(
          result
            .map((e) => {
              const kanaFirst = e.kana[0].toUpperCase();
              e.kana = kanaFirst;
              return e;
            })
            .sort((a, b) => -b.kana.localeCompare(a.kana))
        );
      })
      .catch((e: Error) => {
        errorDefault("想定外エラー!!");
      });
  }, []);

  const [selectSourcingType, setSelectSourcingType] = useState(member ? member.sourcingType : "1");

  const {
    control,
    handleSubmit,
    reset,
    setValue,
    formState: { errors },
  } = useForm<Member>({
    defaultValues: {
      name: member?.name,
      kana: member?.kana,
      sourcingType: selectSourcingType,
      affiliation: member?.affiliation,
      partner: member?.partner,
      contractType: member ? member.contractType : "1",
      gender: member ? member.gender : "1",
      note: member?.note,
    },
  });

  const validationRules = {
    name: nameRule,
    kana: kanaRule,
    affiliation: {
      maxLength: { value: 120, message: "120文字以内で入力してください。" },
    },
    note: noteRune,
  };

  const onSubmit: SubmitHandler<Member> = (input: Member) => {
    setProgress(true);

    if ("c" === mode) {
      input.partnerId = input.partner ? input.partner.partnerId : undefined;
      createMembers([input])
        .then((result: void) => {
          setProgress(false);
          successDefault("登録成功!!");
          handleClose();
          handleUpdate();
        })
        .catch((e: Error) => {
          setProgress(false);
          errorDefault("想定外エラー!!");
          handleClose();
        });
    }

    if ("u" === mode) {
      input.memberId = member!.memberId!;
      input.partnerId = input.partner ? input.partner.partnerId : undefined;
      updateMember(input)
        .then((result: void) => {
          setProgress(false);
          successDefault("更新成功!!");
          handleClose();
          handleUpdate();
        })
        .catch((e: Error) => {
          setProgress(false);
          errorDefault("想定外エラー!!");
          handleClose();
        });
    }
  };

  return (
    <>
      <Container>
        <form onSubmit={handleSubmit((d) => onSubmit(d))} noValidate>
          <Stack direction="column" width="500px" gap={2}>
            <Stack direction="column" gap={1}>
              <FormLabel id="name-group-label">氏名</FormLabel>
              <Stack direction="row" gap={2}>
                <Controller
                  name="name"
                  control={control}
                  rules={validationRules.name}
                  render={({ field, formState: { errors } }) => (
                    <TextField
                      variant="outlined"
                      disabled={"r" === mode}
                      {...field}
                      label="氏名"
                      error={!!errors.name}
                      helperText={errors.name?.message}
                    />
                  )}
                />
                <Controller
                  name="kana"
                  control={control}
                  rules={validationRules.kana}
                  render={({ field, formState: { errors } }) => (
                    <TextField
                      variant="outlined"
                      disabled={"r" === mode}
                      {...field}
                      label="氏名(カナ)"
                      error={!!errors.kana}
                      helperText={errors.kana?.message}
                    />
                  )}
                />
              </Stack>
            </Stack>
            <Controller
              name="sourcingType"
              control={control}
              render={({ field, formState: { errors } }) => (
                <FormControl>
                  <FormLabel id="sourcing-type-group-label">雇用形態</FormLabel>
                  <RadioGroup
                    disabled={"r" === mode}
                    {...field}
                    row
                    name="sourcing-type-group"
                    onChange={(e) => {
                      setValue("sourcingType", e.target.value as "1" | "2");
                      setSelectSourcingType(e.target.value as "1" | "2");
                    }}
                  >
                    <FormControlLabel value="1" control={<Radio />} label={SourcingType["1"]} />
                    <FormControlLabel value="2" control={<Radio />} label={SourcingType["2"]} />
                  </RadioGroup>
                </FormControl>
              )}
            />
            <div hidden={"2" === selectSourcingType}>
              <Controller
                name="affiliation"
                control={control}
                rules={validationRules.affiliation}
                render={({ field, formState: { errors } }) => (
                  <TextField
                    variant="outlined"
                    disabled={"r" === mode}
                    {...field}
                    label="所属"
                    error={!!errors.affiliation}
                    helperText={errors.affiliation?.message}
                  />
                )}
              />
            </div>
            <div hidden={"1" === selectSourcingType}>
              <Controller
                name="partner"
                control={control}
                render={({ field, formState: { errors } }) => (
                  <Autocomplete
                    {...field}
                    sx={{ width: 300 }}
                    options={partners}
                    groupBy={(o) => o.kana}
                    getOptionLabel={(o: Partner) => o.name}
                    onChange={(e, v) => {
                      field.onChange(v);
                    }}
                    renderInput={(params) => (
                      <TextField
                        variant="outlined"
                        {...params}
                        disabled={"r" === mode}
                        label="企業名"
                      />
                    )}
                  />
                )}
              />
            </div>
            <div hidden={"1" === selectSourcingType}>
              <Controller
                name="contractType"
                control={control}
                render={({ field, formState: { errors } }) => (
                  <FormControl>
                    <FormLabel id="contract-type-group-label">契約形態</FormLabel>
                    <RadioGroup {...field} row name="contract-type-group">
                      <FormControlLabel
                        disabled={"r" === mode}
                        value="1"
                        control={<Radio />}
                        label={ContractType["1"]}
                      />
                      <FormControlLabel
                        disabled={"r" === mode}
                        value="2"
                        control={<Radio />}
                        label={ContractType["2"]}
                      />
                      <FormControlLabel
                        disabled={"r" === mode}
                        value="3"
                        control={<Radio />}
                        label={ContractType["3"]}
                      />
                    </RadioGroup>
                  </FormControl>
                )}
              />
            </div>
            <Controller
              name="gender"
              control={control}
              render={({ field, formState: { errors } }) => (
                <FormControl>
                  <FormLabel id="gender-group-label">性別</FormLabel>
                  <RadioGroup disabled={"r" === mode} {...field} row name="gender-group">
                    <FormControlLabel value="1" control={<Radio />} label={Gender["1"]} />
                    <FormControlLabel value="2" control={<Radio />} label={Gender["2"]} />
                    <FormControlLabel value="3" control={<Radio />} label={Gender["3"]} />
                  </RadioGroup>
                </FormControl>
              )}
            />
            <Controller
              name="note"
              control={control}
              rules={validationRules.note}
              render={({ field, formState: { errors } }) => (
                <TextField
                  variant="outlined"
                  disabled={"r" === mode}
                  multiline
                  rows={7}
                  {...field}
                  label="備考"
                  error={!!errors.note}
                  helperText={errors.note?.message}
                />
              )}
            />
            <Button type="submit" variant="contained" color="primary">
              登録
            </Button>
          </Stack>
        </form>
      </Container>
    </>
  );
};

export default MemberEdit;
