import { generateUUID, getFormattedDateTime } from "../../utils/Common";

import { Manager } from "./ManagerStoreAdapter";

export type Team = {
  teamId?: string;
  manager: Manager;
  managerId: string;
  name: string;
  note?: string;
  createdAt?: string;
  createdBy?: string;
  updatedAt?: string;
  updatedBy?: string;
};

export const createTeam = async (team: Team): Promise<void> => {
  await new Promise((res) => {
    setTimeout(res, 1000);
  });

  const storeRaw: string = sessionStorage.getItem(storeName) || "{}";
  const store: Store = JSON.parse(storeRaw);

  let uuid = generateUUID();

  team.teamId = uuid;

  store[uuid] = team;
  sessionStorage.setItem(storeName, JSON.stringify(store));
};

