export const getTeam = async (teamId: string): Promise<Team> => {
  await new Promise((res) => {
    setTimeout(res, 100);
  });

  const storeRaw: string = sessionStorage.getItem(storeName) || "{}";
  const store: Store = JSON.parse(storeRaw);

  return store[teamId];
};

