import { generateUUID, getFormattedDateTime } from "../../utils/Common";

export type Partner = {
  partnerId?: string;
  name: string;
  kana: string;
  partnerType: "1" | "2";
  corporateNumber?: string;
  invoiceRegistrationNumber?: string;
  url?: string;
  note?: string;
  createdAt?: string;
  createdBy?: string;
  updatedAt?: string;
  updatedBy?: string;
};

export const PartnerType = {
  "1": "法人",
  "2": "個人",
} as const;

export const createPartner = async (partners: Partner[]): Promise<void> => {
  await new Promise((res) => {
    setTimeout(res, 1000);
  });

  const storeRaw: string = sessionStorage.getItem(storeName) || "{}";
  const store: Store = JSON.parse(storeRaw);

  partners.forEach((e) => {
    const partnerId = generateUUID();
    const userId = "sample managemer";
    const now = getFormattedDateTime();
    e.partnerId = partnerId;
    e.createdBy = userId;
    e.updatedBy = userId;
    e.createdAt = now;
    e.updatedAt = now;
    store[partnerId] = e;
  });
  sessionStorage.setItem(storeName, JSON.stringify(store));
};

export const updatePartner = async (partner: Partner): Promise<void> => {
  await new Promise((res) => {
    setTimeout(res, 1000);
  });

  const storeRaw: string = sessionStorage.getItem(storeName) || "{}";
  const store: Store = JSON.parse(storeRaw);

  const targer = store[partner.partnerId!];

  if (!targer) {
    await createPartner([partner]);
  }

  const partnerId = targer.partnerId!;
  const userId = "sample managemer update";
  const now = getFormattedDateTime();
  targer.name = partner.name;
  targer.kana = partner.kana;
  targer.partnerType = partner.partnerType;
  targer.corporateNumber = partner.corporateNumber;
  targer.invoiceRegistrationNumber = partner.invoiceRegistrationNumber;
  targer.url = partner.url;
  targer.note = partner.note;
  targer.partnerId = partnerId;
  targer.updatedBy = userId;
  targer.updatedAt = now;
  store[partnerId] = targer;

  sessionStorage.setItem(storeName, JSON.stringify(store));
};

export const getPartner = async (partnerId: string): Promise<Partner> => {
  await new Promise((res) => {
    setTimeout(res, 100);
  });

  const storeRaw: string = sessionStorage.getItem(storeName) || "{}";
  const store: Store = JSON.parse(storeRaw);

  return store[partnerId];
};

export const getPartners = async (): Promise<Partner[]> => {
  await new Promise((res) => {
    setTimeout(res, 1000);
  });

  const storeRaw: string = sessionStorage.getItem(storeName) || "{}";
  const store: Store = JSON.parse(storeRaw);

  const pairs: [string, Partner][] = Object.entries(store);
  const partners: Partner[] = pairs.map((e: [string, Partner]) => e[1]);

  return partners;
};

// backendが構築できるまでの代わり
export const storeName = "partners";
export type Store = { [key: string]: Partner };
