import React, { useState } from "react";

import IconButton from "@mui/material/IconButton";
import Menu from "@mui/material/Menu";
import MenuItem from "@mui/material/MenuItem";
import MoreHorizIcon from "@mui/icons-material/MoreHoriz";

import HomeIcon from "@mui/icons-material/Home";
import HandshakeIcon from "@mui/icons-material/Handshake";
import PeopleAltIcon from "@mui/icons-material/PeopleAlt";
import GroupsIcon from "@mui/icons-material/Groups";
import ManageAccountsIcon from "@mui/icons-material/ManageAccounts";
import TableViewIcon from "@mui/icons-material/TableView";

import { NavigationItem } from "@toolpad/core/AppProvider";

import { errorDefault } from "../../utils/Toastify";

import {
  // model
  Authentication,
  // api
  getSessionUser,
} from "../../stores/common/AuthenticationStoreAdapter";

import {
  // model
  Manager,
  // api
  getManagers,
} from "../../stores/cost/ManagerStoreAdapter";

import {
  // api
  getTeams,
  // model
  Team,
} from "../../stores/cost/TeamsStoreAdapter";

import {
  // model
  Term,
  // api
  getTerms,
} from "../../stores/cost/TermsStoreAdapter";

import { APP_NAME } from "../router/common";

export const navigationItems = (
  setTeams: any,
  setNavigation: any,
  setEditTeam: any,
  handleTeamCreateWindowOpen: any,
  handleMemberAddWindowOpen: any
) => {
  Promise.all([getTerms(), getTeams(), getManagers()])
    .then((results) => {
      const su = getSessionUser();
      const ts: { [key: string]: Team } = {};
      const mtg: { [key: string]: Team[] } = {};
      for (const t of results[1]) {
        const mid = t.managerId;

        if ("1" === su.userId) {
          continue;
        }
        if ("2" === su.role && t.managerId !== su.userId) {
          continue;
        }
        if (!mtg[mid]) {
          mtg[mid] = [];
        }

        const tg = mtg[mid];
        tg.push(t);

        ts[t.teamId!] = t;
      }
      setTeams(ts);

      const items: NavigationItem[] = [];
      items.push({ segment: `${APP_NAME}`, title: "ホーム", icon: <HomeIcon /> });
      items.push({ kind: "divider" });
      items.push({ kind: "header", title: "管理" });
      items.push({
        segment: `${APP_NAME}/cost/partner`,
        title: "パートナー",
        icon: <HandshakeIcon />,
      });
      items.push({ segment: `${APP_NAME}/cost/member`, title: "要員", icon: <PeopleAltIcon /> });

      let m: Manager | null = null;
      for (const ts of Object.values(mtg)) {
        if (!m) {
          m = ts[0].manager;
          items.push({ kind: "divider" });
          items.push({ kind: "header", title: m.name });
        }

        const mangerItem: NavigationItem = {
          segment: `${APP_NAME}/cost/manager/${m.managerId}`,
          title: "管理",
          icon: <ManageAccountsIcon />,
          action: (
            <ManagerMenu
              manager={m}
              setEditTeam={setEditTeam}
              handleTeamCreateWindowOpen={handleTeamCreateWindowOpen}
            />
          ),
          children: [],
        };
        items.push(mangerItem);

        items.push({ kind: "header", title: "チーム" });
        for (const t of ts) {
          const teamItem: NavigationItem = {
            segment: `${APP_NAME}/cost/team/${m.managerId}/${t.teamId}`,
            title: `${t.name}`,
            icon: <GroupsIcon />,
            action: (
              <TeamMenu
                team={t}
                setEditTeam={setEditTeam}
                handleMemberAddWindowOpen={handleMemberAddWindowOpen}
              />
            ),
            children: [],
          };

          const children: NavigationItem[] = [];
          for (const term of results[0]) {
            const child: NavigationItem = {
              segment: `${term.termNumber}`,
              title: `${term.name}(${term.yearFrom}-${term.yearTo})`,
              icon: <TableViewIcon />,
            };
            children.push(child);
          }
          teamItem.children = children;
          items.push(teamItem);
        }
        m = null;
      }
      setNavigation(items);
    })
    .catch((e: Error) => {
      console.error(e);
      errorDefault("想定外エラー!!");
    });
};

const TeamMenu = ({
  team,
  setEditTeam,
  handleMemberAddWindowOpen,
}: {
  team: Team;
  setEditTeam: any;
  handleMemberAddWindowOpen: any;
}) => {
  const [popoverAnchorEl, setPopoverAnchorEl] = useState<HTMLButtonElement | null>(null);

  const isPopoverOpen = Boolean(popoverAnchorEl);
  const popoverId = isPopoverOpen ? `${team.teamId}-popover` : undefined;

  const handlePopoverButtonClick = (event: React.MouseEvent<HTMLButtonElement>) => {
    event.stopPropagation();
    setPopoverAnchorEl(event.currentTarget);
  };

  const handlePopoverClose = (event: React.MouseEvent<HTMLElement>) => {
    event.stopPropagation();
    setPopoverAnchorEl(null);
  };

  const handleMemberAddClick = (event: React.MouseEvent<HTMLElement>) => {
    handlePopoverClose(event);

    setEditTeam(team);
    handleMemberAddWindowOpen();
  };

  return (
    <React.Fragment>
      <IconButton aria-describedby={popoverId} onClick={handlePopoverButtonClick}>
        <MoreHorizIcon />
      </IconButton>
      <Menu
        id={popoverId}
        open={isPopoverOpen}
        anchorEl={popoverAnchorEl}
        onClose={handlePopoverClose}
        anchorOrigin={{
          vertical: "bottom",
          horizontal: "right",
        }}
        disableAutoFocus
        disableAutoFocusItem
      >
        <MenuItem onClick={handleMemberAddClick}>メンバーの追加</MenuItem>
        <MenuItem onClick={handlePopoverClose}>メンバーの削除(未実装)</MenuItem>
      </Menu>
    </React.Fragment>
  );
};

const ManagerMenu = ({
  manager,
  setEditTeam,
  handleTeamCreateWindowOpen,
}: {
  manager: Manager;
  setEditTeam: any;
  handleTeamCreateWindowOpen: any;
}) => {
  const [popoverAnchorEl, setPopoverAnchorEl] = useState<HTMLButtonElement | null>(null);

  const isPopoverOpen = Boolean(popoverAnchorEl);
  const popoverId = isPopoverOpen ? `${manager.managerId}-popover` : undefined;

  const handlePopoverButtonClick = (event: React.MouseEvent<HTMLButtonElement>) => {
    event.stopPropagation();
    setPopoverAnchorEl(event.currentTarget);
  };

  const handlePopoverClose = (event: React.MouseEvent<HTMLElement>) => {
    event.stopPropagation();
    setPopoverAnchorEl(null);
  };

  const handleTeamCreateClick = (event: React.MouseEvent<HTMLElement>) => {
    handlePopoverClose(event);
    const team: Team = {
      manager: manager,
      managerId: manager.managerId,
      name: "",
    };

    setEditTeam(team);
    handleTeamCreateWindowOpen();
  };

  return (
    <React.Fragment>
      <IconButton aria-describedby={popoverId} onClick={handlePopoverButtonClick}>
        <MoreHorizIcon />
      </IconButton>
      <Menu
        id={popoverId}
        open={isPopoverOpen}
        anchorEl={popoverAnchorEl}
        onClose={handlePopoverClose}
        anchorOrigin={{
          vertical: "bottom",
          horizontal: "right",
        }}
        disableAutoFocus
        disableAutoFocusItem
      >
        <MenuItem onClick={handleTeamCreateClick}>チームの追加</MenuItem>
        <MenuItem onClick={handlePopoverClose}>チームの削除(未実装)</MenuItem>
      </Menu>
    </React.Fragment>
  );
};
