import React from "react";
import { useNavigate, Outlet } from "react-router-dom";

import Stack from "@mui/material/Stack";
import Chip from "@mui/material/Chip";
import Typography from "@mui/material/Typography";
import AppsIcon from "@mui/icons-material/Apps";

import { AppProvider, Router } from "@toolpad/core/AppProvider";
import { PageContainer } from "@toolpad/core/PageContainer";
import { DashboardLayout } from "@toolpad/core/DashboardLayout";

import { dashboardTheme, session, signInOut, SidebarFooter } from "./router/common";
import { navigationItems } from "./navigation/index";

import { APP_NAME } from "./router/common";

const IndexLayout: React.FC = () => {
  const navigations = navigationItems();

  const router = useRouter(`/${APP_NAME}`);

  const authentication = React.useMemo(() => signInOut, []);

  return (
    <>
      <AppProvider
        session={session()}
        authentication={authentication}
        navigation={navigations}
        router={router}
        theme={dashboardTheme}
      >
        <DashboardLayout
          hideNavigation
          slots={{
            appTitle: CustomAppTitle,
            sidebarFooter: SidebarFooter,
          }}
        >
          <PageContainer maxWidth="xl">
            <Outlet />
          </PageContainer>
        </DashboardLayout>
      </AppProvider>
    </>
  );
};

function useRouter(initialPath: string): Router {
  const navigate = useNavigate();
  const [pathname, setPathname] = React.useState(initialPath);

  const router = React.useMemo(() => {
    return {
      pathname,
      searchParams: new URLSearchParams(),
      navigate: (path: string | URL) => {
        document.location = `/${APP_NAME}/cost`;
        setPathname(String(path));
      },
    };
  }, [pathname]);

  return router;
}

export function CustomAppTitle() {
  return (
    <>
      <Stack direction="row" alignItems="center" spacing={2}>
        <AppsIcon fontSize="large" color="primary" />
        <Typography color="primary" variant="h6" sx={{ fontWeight: "bold", m: 1 }}>
          InhouseSystem
        </Typography>
        <Chip size="small" label="BETA" color="warning" />
      </Stack>
    </>
  );
}
export default IndexLayout;
