import {
  // api
  getPartner,
  // model
  Partner,
} from "./PartnersStoreAdapter";

import { generateUUID, getFormattedDateTime } from "../../utils/Common";

export type Member = {
  memberId?: string;
  name: string;
  kana: string;
  sourcingType: "1" | "2";
  affiliation?: string;
  partnerId?: string;
  partner?: Partner;
  contractType: "1" | "2" | "3" | "4";
  gender: "1" | "2" | "3";
  birthday?: string;
  note?: string;
  createdAt?: string;
  createdBy?: string;
  updatedAt?: string;
  updatedBy?: string;
};

export const SourcingType = {
  "1": "社員",
  "2": "社外",
} as const;

export const ContractType = {
  "1": "未選択",
  "2": "準委任契約",
  "3": "派遣契約",
  "4": "正社員",
} as const;

export const Gender = {
  "1": "未選択",
  "2": "男",
  "3": "女",
} as const;

export const createMembers = async (members: Member[]): Promise<void> => {
  await new Promise((res) => {
    setTimeout(res, 1000);
  });

  const storeRaw: string = sessionStorage.getItem(storeName) || "{}";
  const store: Store = JSON.parse(storeRaw);

  members.forEach((e) => {
    const memberId = generateUUID();
    const userId = "sample managemer";
    const now = getFormattedDateTime();
    e.memberId = memberId;
    e.createdBy = userId;
    e.updatedBy = userId;
    e.createdAt = now;
    e.updatedAt = now;
    store[memberId] = e;
  });
  sessionStorage.setItem(storeName, JSON.stringify(store));
};

export const updateMember = async (member: Member): Promise<void> => {
  await new Promise((res) => {
    setTimeout(res, 1000);
  });

  const storeRaw: string = sessionStorage.getItem(storeName) || "{}";
  const store: Store = JSON.parse(storeRaw);

  const targer = store[member.memberId!];

  if (!targer) {
    await createMembers([member]);
  }

  const memberId = targer.memberId!;
  const userId = "sample managemer update";
  const now = getFormattedDateTime();
  targer.name = member.name;
  targer.kana = member.kana;
  targer.sourcingType = member.sourcingType;
  targer.affiliation = "2" === member.sourcingType ? undefined : member.affiliation;
  targer.partnerId = "1" === member.sourcingType ? undefined : member.partnerId;
  targer.contractType = "1" === member.sourcingType ? "4" : member.contractType;
  targer.gender = member.gender;
  targer.note = member.note;
  targer.updatedBy = userId;
  targer.updatedAt = now;
  store[memberId] = targer;

  sessionStorage.setItem(storeName, JSON.stringify(store));
};

export const getMember = async (memberId: string): Promise<Member> => {
  await new Promise((res) => {
    setTimeout(res, 1000);
  });

  const storeRaw: string = sessionStorage.getItem(storeName) || "{}";
  const store: Store = JSON.parse(storeRaw);

  const member = store[memberId]!;

  if (member.partnerId) {
    const partner = await getPartner(member.partnerId!);
    member.partner = partner;
  }
  return member;
};

export const getMembers = async (): Promise<Member[]> => {
  await new Promise((res) => {
    setTimeout(res, 1000);
  });

  const storeRaw: string = sessionStorage.getItem(storeName) || "{}";
  const store: Store = JSON.parse(storeRaw);

  const pairs: [string, Member][] = Object.entries(store);
  const members: Member[] = pairs.map((e: [string, Member]) => e[1]);
  for (const m of members) {
    const partner: Partner = await getPartner(m.partnerId!);
    m.partner = partner;
  }
  return members;
};

// backendが構築できるまでの代わり
export const storeName = "members";
export type Store = { [key: string]: Member };
