import React from "react";

import { useLocation } from "react-router-dom";

import { useForm, SubmitHandler, Controller } from "react-hook-form";

import { TextField, Stack, Button, Container } from "@mui/material";

import { successDefault, errorDefault } from "../../../utils/Toastify";

import {
  // model
  Team,
  // api
  createTeam,
} from "../../../stores/cost/TeamsStoreAdapter";

import { nameRule, noteRune } from "../../../rule/Validation";

export interface TeamEditProps {
  mode: "c" | "r" | "u";
  team: Team;
  handleUpdate: any;
  handleClose: any;
  setProgress: any;
}

const TeamEdit: React.FC<TeamEditProps> = ({
  mode,
  team,
  handleUpdate,
  handleClose,
  setProgress,
}) => {
  const location = useLocation();

  const {
    control,
    handleSubmit,
    reset,
    setValue,
    formState: { errors },
  } = useForm<Team>({
    defaultValues: {
      name: team.name,
      note: team.note,
    },
  });

  const validationRules = {
    name: nameRule,
    note: noteRune,
  };

  const onSubmit: SubmitHandler<Team> = (input: Team) => {
    setProgress(true);

    if ("c" === mode) {
      team.name = input.name;
      team.note = input.note;
      createTeam(team)
        .then((result: void) => {
          setProgress(false);
          successDefault("登録成功!!");
          handleClose();
          handleUpdate();
        })
        .catch((e: Error) => {
          setProgress(false);
          errorDefault("想定外エラー!!");
          handleClose();
        });
    }

    if ("u" === mode) {
      //   input.memberId = member!.memberId!;
      //   input.partnerId = input.partner ? input.partner.partnerId : undefined;
      //   updateMember(input)
      //     .then((result: void) => {
      //       setProgress(false);
      //       successDefault("更新成功!!");
      //       handleClose();
      //       handleUpdate();
      //     })
      //     .catch((e: Error) => {
      //       setProgress(false);
      //       errorDefault("想定外エラー!!");
      //       handleClose();
      //     });
    }
  };

  return (
    <>
      <Container>
        <form onSubmit={handleSubmit((d) => onSubmit(d))} noValidate>
          <Stack direction="column" width="500px" gap={2}>
            <Controller
              name="name"
              control={control}
              rules={validationRules.name}
              render={({ field, formState: { errors } }) => (
                <TextField
                  variant="outlined"
                  disabled={"r" === mode}
                  {...field}
                  label="チーム名"
                  error={!!errors.name}
                  helperText={errors.name?.message}
                />
              )}
            />
            <Controller
              name="note"
              control={control}
              rules={validationRules.note}
              render={({ field, formState: { errors } }) => (
                <TextField
                  variant="outlined"
                  disabled={"r" === mode}
                  multiline
                  rows={7}
                  {...field}
                  label="備考"
                  error={!!errors.note}
                  helperText={errors.note?.message}
                />
              )}
            />
            <Button type="submit" variant="contained" color="primary">
              登録
            </Button>
          </Stack>
        </form>
      </Container>
    </>
  );
};

export default TeamEdit;
