import { BrowserRouter, Route, Routes } from "react-router-dom";

import { ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

import IndexRoute from "./routes/IndexRoute";
import Home from "./pages/common/Home";
import NotImplemented from "./pages/common/NotImplemented";

import Login from "./pages/common/Login";

import CostRoute from "./routes/CostRoute";
import CostIndex from "./pages/cost/Index";

import Partner from "./pages/cost/Partner";
import Member from "./pages/cost/Member";
import Team from "./pages/cost/Team";

import { APP_NAME } from "./layouts/router/common";

function App() {
  return (
    <>
      <BrowserRouter>
        <Routes>
          <Route path={`/${APP_NAME}/login`} Component={Login} />
          <Route path={`/${APP_NAME}`} Component={IndexRoute}>
            <Route index Component={Home} />
            <Route path={`/${APP_NAME}/*`} Component={NotImplemented} />
            <Route path={`/${APP_NAME}/work/*`} Component={NotImplemented} />
          </Route>

          <Route path={`/${APP_NAME}/cost`} Component={CostRoute}>
            <Route index Component={CostIndex} />
            <Route path={`/${APP_NAME}/cost/partner`} Component={Partner} />
            <Route path={`/${APP_NAME}/cost/member`} Component={Member} />
            {/* <Route path={`/${APP_NAME}/cost/team/:managerId/:teamId`} Component={Team2} /> */}
            <Route
              path={`/${APP_NAME}/cost/team/:managerId/:teamId/:termNumber`}
              Component={Team}
            />
            <Route path={`/${APP_NAME}/cost/*`} Component={NotImplemented} />
          </Route>
        </Routes>
      </BrowserRouter>
      <ToastContainer
        position="top-right"
        autoClose={2000}
        hideProgressBar={false}
        newestOnTop={false}
        closeOnClick
        rtl={false}
        pauseOnFocusLoss
        draggable
        pauseOnHover
      />
    </>
  );
}

export default App;
