import React from "react";

import CostLayout from "../layouts/CostLayout";

import { APP_NAME } from "../layouts/router/common";

const CostRoute: React.FC = () => {
  const sessionUserId = sessionStorage.getItem("session-user");

  if (sessionUserId == null) {
    document.location = `/${APP_NAME}/login`;
    return <></>;
  }
  return <CostLayout />;
};

export default CostRoute;
