import React, { useEffect, useState } from "react";

import { useLocation } from "react-router-dom";

import { Stack, Grid, IconButton, Divider } from "@mui/material";
import AddCircleOutlineIcon from "@mui/icons-material/AddCircleOutline";

import PartnerEdit from "./components/PartnerEdit";

import Circular from "../../components/CustomCircular";
import SubWindow from "../../components/SubWindow";
import StickyHeaderTable, { Column } from "../../components/table/StickyHeaderTable";

import { errorDefault } from "../../utils/Toastify";
import {
  // api
  getPartners,
  // model
  Partner,
  PartnerType,
} from "../../stores/cost/PartnersStoreAdapter";

const PartnerPage: React.FC = () => {
  const location = useLocation();

  const [isProgress, setProgress] = useState(true);

  const [isCreateOpen, setCreateOpen] = useState(false);
  const [isEditOpen, setEditOpen] = useState(false);

  const [partners, setPartners] = useState<Partner[]>([]);
  const [editPartner, setEditPartner] = useState<Partner>();

  useEffect(() => {
    getPartners()
      .then((result: Partner[]) => {
        setPartners(result);
        setProgress(false);
      })
      .catch((e: Error) => {
        setProgress(false);
        errorDefault("想定外エラー!!");
      });
  }, []);

  const handleUpdate = () => {
    getPartners()
      .then((result: Partner[]) => {
        setPartners(result);
        setProgress(false);
      })
      .catch((e: Error) => {
        setProgress(false);
        errorDefault("想定外エラー!!");
      });
  };

  const handleCreateOpen = () => {
    setCreateOpen(true);
  };

  const handleCreateClose = () => {
    setCreateOpen(false);
  };

  const handleEditOpen = (partner: Partner) => {
    setEditPartner(partner);
    setEditOpen(true);
  };

  const handleEditClose = () => {
    setEditOpen(false);
  };

  return (
    <>
      <Stack spacing={5} divider={<Divider flexItem />}>
        <Grid container spacing={1}>
          <Grid size={0.5}></Grid>
          <Grid size={2}>
            <IconButton onClick={handleCreateOpen}>
              <AddCircleOutlineIcon fontSize="large" />
            </IconButton>
          </Grid>
          <Grid size={9.5}></Grid>
        </Grid>
        <StickyHeaderTable headers={headers} records={partners} clickAction={handleEditOpen} />
      </Stack>
      <SubWindow title="企業の登録" open={isCreateOpen} handleClose={handleCreateClose}>
        <PartnerEdit
          mode="c"
          handleUpdate={handleUpdate}
          handleClose={handleCreateClose}
          setProgress={setProgress}
        />
      </SubWindow>
      <SubWindow title="企業の編集" open={isEditOpen} handleClose={handleEditClose}>
        <PartnerEdit
          partner={editPartner}
          mode="u"
          handleUpdate={handleUpdate}
          handleClose={handleEditClose}
          setProgress={setProgress}
        />
      </SubWindow>
      <Circular open={isProgress} />
    </>
  );
};

const headers: readonly Column[] = [
  { id: "name", label: "企業名", minWidth: 200, align: "center" },
  { id: "kana", label: "企業名(カナ)", minWidth: 200, align: "center" },
  {
    id: "partnerType",
    label: "法/個",
    minWidth: 100,
    align: "center",
    format: (v: Partner) => PartnerType[v.partnerType],
  },
  { id: "corporateNumber", label: "法人番号", minWidth: 150, align: "center" },
  { id: "invoiceRegistrationNumber", label: "登録番号", minWidth: 150, align: "center" },
  { id: "createdAt", label: "登録日", minWidth: 150, align: "center" },
  { id: "createdBy", label: "登録者", minWidth: 150, align: "center" },
  { id: "updatedAt", label: "最終更新日", minWidth: 150, align: "center" },
  { id: "updatedBy", label: "最終更新者", minWidth: 150, align: "center" },
];

export default PartnerPage;
