import React, { useState } from "react";

import { styled } from "@mui/material/styles";

import { useLocation } from "react-router-dom";

import { Alert, Badge } from "@mui/material";

import { CardHeader, CardContent, CardActions } from "@mui/material";
import { Typography, Divider, Stack, Collapse } from "@mui/material";

import MuiAccordionSummary, {
  AccordionSummaryProps,
  accordionSummaryClasses,
} from "@mui/material/AccordionSummary";

import Accordion from "@mui/material/Accordion";
import AccordionDetails from "@mui/material/AccordionDetails";

import KeyboardArrowRightIcon from "@mui/icons-material/KeyboardArrowRight";

import Button from "@mui/material/Button";

import { Notice, getNotices, getImportantNotices } from "../notice/notice";

const IndexNotice: React.FC = () => {
  const location = useLocation();

  const latestNotices: Notice[] = getNotices().filter((e) => e.isLatest);
  const historyNotices: Notice[] = getNotices().filter((e) => !e.isLatest);
  const latestImportants: Notice[] = getImportantNotices().filter((e) => e.isLatest);
  const historyImportants: Notice[] = getImportantNotices().filter((e) => !e.isLatest);

  const [noticeMore, setNoticeMore] = useState(false);
  const [importantMore, setImportantMore] = useState(false);

  return (
    <>
      <Accordion defaultExpanded={0 < latestImportants.length}>
        <AccordionSummary>
          <Badge
            sx={{ width: "100%" }}
            anchorOrigin={{
              vertical: "top",
              horizontal: "left",
            }}
            color="error"
            badgeContent={latestImportants.length}
            variant="dot"
          >
            <Typography component="span" sx={{ flexShrink: 0 }}>
              重要なお知らせ
            </Typography>
          </Badge>
        </AccordionSummary>
        <AccordionDetails>
          <Stack spacing={3} sx={{ mt: 2 }}>
            {0 < latestImportants.length ? (
              <>
                {latestImportants.map((e) => {
                  return <ImportantArea e={e}></ImportantArea>;
                })}
              </>
            ) : (
              <Typography variant="h6">最新のお知らせはありません。</Typography>
            )}
            <Collapse in={importantMore} timeout="auto" unmountOnExit>
              <Divider />
              <Stack spacing={3} sx={{ mt: 2 }}>
                {historyImportants.map((e) => {
                  return <ImportantArea e={e} />;
                })}
              </Stack>
            </Collapse>
            <CardActions>
              {importantMore ? (
                <Button size="small" onClick={() => setImportantMore(false)}>
                  過去の重要なお知らせを閉じる
                </Button>
              ) : (
                <Button size="small" onClick={() => setImportantMore(true)}>
                  過去の重要なお知らせを見る
                </Button>
              )}
            </CardActions>
          </Stack>
        </AccordionDetails>
      </Accordion>
      <Accordion>
        <AccordionSummary>
          <Badge
            sx={{ width: "100%" }}
            anchorOrigin={{
              vertical: "top",
              horizontal: "left",
            }}
            color="primary"
            badgeContent={latestNotices.length}
            variant="dot"
          >
            <Typography component="span" sx={{ flexShrink: 0 }}>
              お知らせ
            </Typography>
          </Badge>
        </AccordionSummary>
        <AccordionDetails>
          <Stack spacing={3} sx={{ mt: 2 }}>
            {0 < latestNotices.length ? (
              <>
                {latestNotices.map((e) => {
                  return <NoticeArea e={e}></NoticeArea>;
                })}
              </>
            ) : (
              <Typography variant="h6">最新のお知らせはありません。</Typography>
            )}
            <Collapse in={noticeMore} timeout="auto" unmountOnExit>
              <Divider />
              <Stack spacing={3} sx={{ mt: 2 }}>
                {historyNotices.map((e) => {
                  return <NoticeArea e={e}></NoticeArea>;
                })}
              </Stack>
            </Collapse>
            <CardActions>
              {noticeMore ? (
                <Button size="small" onClick={() => setNoticeMore(false)}>
                  過去のお知らせを閉じる
                </Button>
              ) : (
                <Button size="small" onClick={() => setNoticeMore(true)}>
                  過去のお知らせを見る
                </Button>
              )}
            </CardActions>
          </Stack>
        </AccordionDetails>
      </Accordion>
    </>
  );
};

function NoticeArea({ e }: { e: Notice }) {
  return (
    <Alert variant="outlined" severity={e.isLatest ? "info" : "success"} sx={{ pt: 0, m: 0 }}>
      <CardHeader
        sx={{ width: "100%", pt: 1, pl: 2, pr: 0, pb: 1 }}
        title={
          <Typography sx={{ pt: 0, m: 0 }} variant="subtitle2" color="textDisabled">
            {e.date}
          </Typography>
        }
        subheader={
          <Typography sx={{ pt: 0, m: 0 }} variant="subtitle1">
            {e.title}
          </Typography>
        }
      />
      <CardContent sx={{ pt: 0, pl: 2, pr: 0, pb: 0 }}>
        <Typography variant="subtitle2">{e.content}</Typography>
      </CardContent>
    </Alert>
  );
}

function ImportantArea({ e }: { e: Notice }) {
  return (
    <Alert
      variant="outlined"
      severity={e.isResolved ? "success" : e.isLatest ? "error" : "warning"}
      sx={{ pt: 0, m: 0 }}
    >
      <CardHeader
        sx={{ width: "100%", pt: 1, pl: 2, pr: 0, pb: 1 }}
        title={
          <Typography sx={{ pt: 0, m: 0 }} variant="subtitle2" color="textDisabled">
            {e.date}
          </Typography>
        }
        subheader={
          <Typography sx={{ pt: 0, m: 0 }} variant="subtitle1">
            {e.title}
          </Typography>
        }
      />
      <CardContent sx={{ pt: 0, pl: 2, pr: 0, pb: 0 }}>
        <Typography variant="subtitle2">{e.content}</Typography>
      </CardContent>
    </Alert>
  );
}

const AccordionSummary = styled((props: AccordionSummaryProps) => (
  <MuiAccordionSummary expandIcon={<KeyboardArrowRightIcon />} {...props} />
))(({ theme }) => ({
  backgroundColor: "rgba(0, 0, 0, .03)",
  flexDirection: "row-reverse",
  [`& .${accordionSummaryClasses.expandIconWrapper}.${accordionSummaryClasses.expanded}`]: {
    transform: "rotate(90deg)",
  },
  [`& .${accordionSummaryClasses.content}`]: {
    marginLeft: theme.spacing(1),
  },
  ...theme.applyStyles("dark", {
    backgroundColor: "rgba(255, 255, 255, .05)",
  }),
}));

export default IndexNotice;
