import { Breadcrumb } from "@toolpad/core";

import {
  // model
  Team,
} from "../../stores/cost/TeamsStoreAdapter";

import { Term } from "../../stores/cost/TermsStoreAdapter";

import { APP_NAME } from "./common";

export const RouterTitle = (pathname: string, teams: { [key: string]: Team }) => {
  if (isHome(pathname)) {
    return "CostManagementSystem";
  }
  if (isPartner(pathname)) {
    return "パートナー";
  }
  if (isMember(pathname)) {
    return "要員";
  }

  const teamParam = matchPath(`/${APP_NAME}/cost/team/:managerId/:teamId/:termNumber`, pathname);
  if (teamParam && teamParam.length == 4) {
    const team = teams[teamParam[2]];
    return team.name;
  }

  return undefined;
};

export const RouterBreadcrumbs = (pathname: string, teams: { [key: string]: Team }) => {
  if (isHome(pathname)) {
    return [];
  }
  if (isPartner(pathname) || isMember(pathname)) {
    return [
      { title: "トップ", path: `/${APP_NAME}/cost` },
      { title: "管理", path: `/${APP_NAME}/cost/mgr` },
    ];
  }

  const teamParam = matchPath(`/${APP_NAME}/cost/team/:managerId/:teamId/:termNumber`, pathname);
  if (teamParam && teamParam.length == 4) {
    const teamId = teamParam[2];
    return extractTeamBreadcrumbs(teamId, teams);
  }

  return undefined;
};

const extractTeamBreadcrumbs = (teamId: string, teams: { [key: string]: Team }): Breadcrumb[] => {
  const team = teams[teamId];
  return [
    { title: "トップ", path: `/${APP_NAME}/cost` },
    { title: team.manager.name, path: `/${APP_NAME}/cost/manager/${team.managerId}` },
    { title: "チーム", path: `/${APP_NAME}/cost/manager/${team.managerId}` },
  ];
};

export const root = `/${APP_NAME}/cost`;

const isHome = (pathname: string) => pathname === root;

const isPartner = (pathname: string) => pathname === `${root}/partner`;

const isMember = (pathname: string) => pathname === `${root}/member`;

function matchPath(pattern: string, pathname: string): string[] | null {
  const regex = new RegExp(`^${pattern.replace(/:[^/]+/g, "([^/]+)")}$`);
  const match = pathname.match(regex);
  return match ? match : null;
}
