import { toast } from "react-toastify";

// see:https://fkhadra.github.io/react-toastify/introduction/
// see:https://qiita.com/pikimaru/items/b2eac418291b8c175c4c
export const successDefault = (message: string) => {
  toast.success(message, {
    position: "top-right",
    autoClose: 2000,
  });
};

export const errorDefault = (message: string) => {
  toast.error(message, {
    position: "top-right",
    autoClose: 5000,
  });
};
