import React, { useState } from "react";

import { useLocation } from "react-router-dom";

import { useForm, SubmitHandler, Controller } from "react-hook-form";

import {
  TextField,
  Stack,
  Button,
  Container,
  Radio,
  RadioGroup,
  FormControlLabel,
  FormLabel,
  InputLabel,
  MenuItem,
  FormControl,
  Select,
} from "@mui/material";
import { SourcingType } from "../../../stores/cost/MembersStoreAdapter";

import { Condition } from "../Team";

export interface ConditionEditProps {
  condition: Condition;
  handleUpdate: any;
  handleClose: any;
}

type Input = {
  name?: string;
  sourcingType: string;
  column: "name";
  order: "asc" | "desc";
};

const ConditionEdit: React.FC<ConditionEditProps> = ({ condition, handleUpdate, handleClose }) => {
  const location = useLocation();

  const {
    control,
    handleSubmit,
    formState: { errors },
  } = useForm<Input>({
    defaultValues: {
      name: condition.name,
      sourcingType: condition.sourcingType,
      column: condition.order.column,
      order: condition.order.order,
    },
  });

  const onSubmit: SubmitHandler<Input> = (input: Input) => {
    const condition: Condition = {
      name: input.name,
      sourcingType: input.sourcingType,
      order: { column: input.column, order: input.order },
    };
    handleUpdate(condition);
    handleClose();
  };

  return (
    <>
      <Container>
        <form onSubmit={handleSubmit((d) => onSubmit(d))} noValidate>
          <Stack direction="column" width="500px" gap={2}>
            <Controller
              name="name"
              control={control}
              render={({ field, formState: { errors } }) => (
                <TextField
                  variant="outlined"
                  {...field}
                  label="名前"
                  error={!!errors.name}
                  helperText={errors.name?.message}
                />
              )}
            />
            <Controller
              name="sourcingType"
              control={control}
              render={({ field, formState: { errors } }) => (
                <FormControl>
                  <FormLabel id="sourcing-type-group-label">雇用形態</FormLabel>
                  <RadioGroup {...field} row name="sourcing-type-group">
                    <FormControlLabel value="0" control={<Radio />} label="未選択" />
                    <FormControlLabel value="1" control={<Radio />} label={SourcingType["1"]} />
                    <FormControlLabel value="2" control={<Radio />} label={SourcingType["2"]} />
                  </RadioGroup>
                </FormControl>
              )}
            />

            <InputLabel id="sort-label">ソート</InputLabel>
            <Controller
              name="column"
              control={control}
              render={({ field, formState: { errors } }) => (
                <FormControl fullWidth>
                  <InputLabel id="select-label">カラム</InputLabel>
                  <Select labelId="select-label" id="select" label="Select" {...field}>
                    <MenuItem value="name">名前</MenuItem>
                  </Select>
                </FormControl>
              )}
            />
            <Controller
              name="order"
              control={control}
              render={({ field, formState: { errors } }) => (
                <FormControl>
                  <RadioGroup {...field} row name="order-group">
                    <FormControlLabel value="asc" control={<Radio />} label="昇順" />
                    <FormControlLabel value="desc" control={<Radio />} label="降順" />
                  </RadioGroup>
                </FormControl>
              )}
            />

            <Button type="submit" variant="contained" color="primary">
              検索
            </Button>
          </Stack>
        </form>
      </Container>
    </>
  );
};

export default ConditionEdit;
